import { Component, OnInit } from '@angular/core';

declare let fbq : Function;
declare let ga  : Function;

@Component({
  selector: 'checkcars-vehicle-success',
  templateUrl: './vehicle-success.component.html',
  styleUrls: ['./vehicle-success.component.scss']
})
export class VehicleSuccessComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
    fbq('track', 'Lead');

    ga ( 'send' , {
      hitType: 'event',
      eventCategory: 'Kontaktaufnahme',
      eventAction: 'Formular abgesendet'
    } );
  }

}
