import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { environment } from '@checkcars/environment/environment';
 
@Component({
  selector: 'checkcars-contact-form',
  templateUrl: './contact-form.component.html',
  styleUrls: ['./contact-form.component.scss'],
})
export class ContactFormComponent implements OnInit {
  @Output() sendOfferRequest: EventEmitter<any> = new EventEmitter(null);
  notSubmitted = true;
  hasError = false;
  siteKey: string;
  callingTimes = [
    '07:00-09:00',
    '09:00-11:00',
    '11:00-13:00',
    '13:00-15:00',
    '15:00-17:00',
    '17:00-19:00',
    '19:00-21:00',
  ];
  contactForm: FormGroup;
  constructor(private fb: FormBuilder) {}

  ngOnInit() {
    this.initContactForm();
    this.siteKey = environment.recaptchaKey;
  }
  initContactForm() {
    this.contactForm = this.fb.group({
      Type: ['Credit', Validators.required],
      Name: [null, Validators.required],
      Phone: [null, Validators.required],
      Email: [null, [Validators.required, CustomValidators.HubSpotTLDEmailValidator]],
      City: [''],
      CallTime: [null, Validators.required],
      TermsAccepted: [false, [Validators.required, CustomValidators.accepted]],
      recaptcha: ['', Validators.required],
      Message: [''],
    });
  }
  submitContatctForm() {
    if (this.contactForm.invalid) {
      console.log( "error please fill form correct" );
      return;
    }

    console.log(this.contactForm.value);
    this.sendOfferRequest.emit(this.contactForm.value);
  }
}

