import { AbstractControl } from '@angular/forms';

export class TrieNode{
  public children: Map <String, TrieNode>;
  public character : string;
  public isLeaf : boolean;
  constructor( key?: string ){
    this.children = new Map <String, TrieNode>();
    this.character = key;
    this.isLeaf = false;
  }
}

export class Trie{
  private root: TrieNode;

  constructor(){
    this.root = new TrieNode();
  }

  public insert( key : string ) : void{
    let children = this.root.children;
    let level = 0;

    for( const char of key ){
      let currNode;
      if( children.get( char ) )
        currNode = children.get( char );
      else{
        currNode = new TrieNode( char );
        children.set( char, currNode );
      }
      children = currNode.children;
      if( level++ == key.length - 1 )
        currNode.isLeaf = true;
    }
  }

  public search( req : string ) : boolean {
    const node = this.getNode( req );

    return ( (node && node.isLeaf) ? true : false );
  }

  public getNode( key : string ) : TrieNode {
    let node = null;
    let currNode = this.root.children;
    for( const char of key ){
      if( currNode.get( char ) ){
        node = currNode.get( char );
        currNode = node.children;
      } else
          return null;
    }
    return node;
  }

}

