export class CustomValidators {
  public static accepted(control: AbstractControl) {
    return !control.value ? { accepted: true } : null;
  }

  public static HubSpotTLDEmailValidator( control: AbstractControl ){
    let TLDs = new HubSpotTLDEmail();
    //console.log( control );
    let currEmail = control.value;
    if( !(control.value && TLDs.search( currEmail.split('.').pop())) )
      return { accepted: true };
    else
      return null;
  }

}
