import {
  Component,
  OnInit,
  Input,
  OnChanges,
  SimpleChanges,
  Output,
  EventEmitter,
  OnDestroy,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FinancialCondition } from 'libs/product-details/src/lib/utils/models/FinancialCondiaiton';
import { ReplaySubject } from 'rxjs';
import { environment } from '@checkcars/environment/environment';
import { SharedFacade, LocalStorageService } from '@checkcars/shared';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'checkcars-offer-box',
  templateUrl: './offer-box.component.html',
  styleUrls: ['./offer-box.component.scss'],
})
export class OfferBoxComponent implements OnInit, OnChanges, OnDestroy {
  conditionLeasing: any;
  conditionCredit: any;
  translations;
  role:string = null;
  lead_add_link:string;
  private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);
  @Input() vatRate;
  @Input() vehicle;
  @Input() priceType: 1 | 2;
  @Output() scrollToContact: EventEmitter<boolean> = new EventEmitter();
  @Output() scrollTo: EventEmitter<string> = new EventEmitter();
  constructor(
    private sharedFacade: SharedFacade,
    private localStorageService:LocalStorageService,
  ) {}
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.priceType && changes.priceType.currentValue) {
      this.priceType = changes.priceType.currentValue;
    }

    if (changes.vehicle && changes.vehicle.currentValue) {
      const vehicleValue = changes.vehicle.currentValue;
      if (vehicleValue.CreditCondition) {
        this.conditionCredit = vehicleValue.CreditCondition;
      }
      if (vehicleValue.LeasingCondition) {
        this.conditionLeasing = vehicleValue.LeasingCondition;
      }
    }
  }
  ngOnInit() {
    this.lead_add_link = environment.lead_add;
    this.loadUser();
  }
  scrollToContactForm() {
    this.scrollToContact.emit(true);
  }
  triggerScroll(term: 'creditTerms' | 'financialTerm') {
    this.scrollTo.emit(term);
  }
  loadUser(){
    this.role =  this.localStorageService.getCookie('userRole');
    /*this.sharedFacade.user$
      .pipe(takeUntil(this.destroyed$))
      .subscribe((res) => {
        console.log('Role',res)
        this.role = res.Role;
      });*/
  }
  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }
}
